$colors: 250;

.product-detail {
  display: flex;
  flex-direction: column;
  width: 320px;

  &__image {
    width: 100%;
  }

  &__name {
    text-align: left;
    padding: 1em 0;
    font-size: 20px;
  }
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 15px;
  display: flex;
}
.left-column {
  width: 50%;
  position: relative;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.right-column {
  width: 50%;
  margin-top: 60px;
}

/* Left Column */
.left-column img {
  transition: all 0.3s ease;
  width: 280px;
}

.left-column img.active {
  opacity: 1;
}

/* Product Description */
.product-description {
  border-bottom: 1px solid #e1e8ee;
  margin-bottom: 20px;
}
.product-description span {
  font-size: 12px;
  color: #358ed7;
  letter-spacing: 1px;
  text-transform: uppercase;
  text-decoration: none;
}
.product-description h1 {
  font-weight: 300;
  font-size: 52px;
  color: #43484d;
  letter-spacing: -2px;
}
.product-description p {
  font-size: 16px;
  font-weight: 300;
  color: #86939e;
  line-height: 24px;
}

p.product-ref {
  font-size: 28px;
  font-weight: bold;
}

.product-sizes__link a {
  color: #86939e;
}

/* Product Color */
.product-feature {
  margin-bottom: 30px;
  border-bottom: 1px solid #e1e8ee;
  padding-bottom: 1em;
  &__title {
    margin-bottom: 12px;
    display: block;
  }
}

.color-choose div {
  display: inline-block;
}

.color-choose input[type="radio"] {
  display: none;
}

.color-choose input[type="radio"] + label span {
  display: inline-block;
  width: 40px;
  height: 40px;
  margin: -1px 4px 0 0;
  vertical-align: middle;
  cursor: pointer;
  border-radius: 50%;
}
.color-choose.color-choose--small input[type="radio"] + label span {
  width: 20px;
  height: 20px;
}

.color-choose input[type="radio"] + label span {
  border: 2px solid #ffffff;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.33);
}

.color-choose input[type="radio"]#red + label span {
  background-color: #c91524;
}
.color-choose input[type="radio"]#blue + label span {
  background-color: #314780;
}
.color-choose input[type="radio"]#black + label span {
  background-color: #323232;
}

// .color-choose input[type=radio]:checked + label span {
//   // background-repeat: no-repeat;
//   // background-position: center;
// }

@mixin colors-generator {
  @for $i from 0 through $colors {
    .color-choose input[type="radio"] + label span.color-#{$i} {
      background-image: url("https://www.velillaconfeccion.com/media/product_matrix/" + $i + ".png");
      background-position: 50% 50%;
    }
  }
}
@mixin mixed-colors-generator {
  @for $i from 0 through $colors {
    .color-choose input[type="radio"] + label span.color-19-#{$i} {
      background-image: url("https://www.velillaconfeccion.com/media/product_matrix/19-" + $i + ".png");
      background-position: 50% 50%;
    }
  }
  @for $i from 0 through $colors {
    .color-choose input[type="radio"] + label span.color-20-#{$i} {
      background-image: url("https://www.velillaconfeccion.com/media/product_matrix/20-" + $i + ".png");
      background-position: 50% 50%;
    }
  }
}

@include colors-generator();
@include mixed-colors-generator();

/* Responsive */
@media (max-width: 940px) {
  .container {
    flex-direction: column;
    margin-top: 60px;
  }

  .left-column,
  .right-column {
    width: 100%;
  }

  .left-column img {
    width: 300px;
    right: 0;
    top: -65px;
    left: initial;
  }
}

@media (max-width: 535px) {
  .left-column img {
    width: 220px;
    top: -85px;
  }
}
